html, body {
  margin: 0;
  padding: 0;
  width: 100vw; /* Full viewport width */
  height: 100vh; /* Full viewport height */
  overflow-y: auto; /* Allow vertical scrolling */
  overflow-x: hidden; /* Prevent horizontal scrolling */
  display: flex;
  flex-direction: column; /* Stack elements vertically */
  justify-content: flex-start; /* Start content below the navbar */
  align-items: center; /* Center horizontally */
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif; /* Consistent font family */
  -webkit-font-smoothing: antialiased; /* Smooth font rendering */
  -moz-osx-font-smoothing: grayscale; /* Smooth font rendering on macOS */
  background-color: #f9f9f9; /* Light background for contrast */
}

body {
  padding-top: 60px; /* Offset for the fixed navbar */
  padding-top: 60px !important; /* Prevent overlap with the fixed header */
  margin: 0; /* Remove any margin above the header */
  padding-top: 0; /* Remove any padding above the header */
}

/* iPhone-specific styles */
@media screen and (max-width: 480px) and (-webkit-device-pixel-ratio: 2) {
  html, body {
    width: 100%; /* Full width */
    margin: 0; /* Remove margins */
    overflow-y: auto; /* Enable vertical scrolling */
    overscroll-behavior: contain; /* Prevent bounce scrolling */
    -webkit-overflow-scrolling: touch; /* Enable smooth scrolling */
    touch-action: manipulation; /* Optimize touch interactions */
    align-items: center; /* Center horizontally only */
  }

  .form-control, .eyewitness-form > button, select.time-dropdown {
    width: 96%; /* Ensure inputs are fully visible */
    margin: 0 auto; /* Center horizontally */
  }
}

/* Android-specific styles */
@media screen and (max-width: 480px) and (min-device-pixel-ratio: 1.5) {
  html, body {
    overscroll-behavior: auto; /* Allow natural scrolling */
    scroll-behavior: smooth; /* Enable smooth scrolling */
    touch-action: pan-y; /* Allow vertical scrolling only */
  }
  .form-control, .eyewitness-form > button {
    width: 96%; /* Shrink to 96% width */
    margin: 0 2%; /* Add 2% left and right margins */
  }
}

@media screen and (max-width: 480px) {
  html, body {
    padding: 10px; /* Add padding for smaller screens */
  }
}

@media print {
  body {
    width: 8.5in; /* Set body width to 8.5 inches */
    height: 11in; /* Set body height to 11 inches */
    margin: 0; /* Remove margins */
    padding: 0; /* Remove padding */
    overflow: visible; /* Ensure content is fully visible */
  }
}

#root {
  width: 100%; /* Ensure root spans 100% width */
  max-width: 100%; /* Ensure it spans the entire width */
  height: auto; /* Auto height */
  min-height: 100vh; /* Minimum full viewport height */
  margin: 0; /* Remove margin */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace; /* Ensure consistent font family for code */
}

button {
  min-width: 44px; /* Ensure minimum touch target size */
  min-height: 44px; /* Ensure minimum touch target size */
  padding: 12px; /* Add padding for better touch interaction */
  font-size: 16px; /* Ensure text is readable */
  border-radius: 8px; /* Add rounded corners */
  background-color: #007aff; /* Use Apple's blue for buttons */
  color: white; /* Ensure text is readable */
  border: none; /* Remove border */
  cursor: pointer; /* Add pointer cursor */
}

button:active {
  background-color: #005bb5; /* Darken button color on press */
  transform: scale(1.03); /* Limit expansion to 3% */
  transition: transform 0.1s ease-in-out; /* Smooth transition */
}

input {
  min-height: 44px; /* Ensure minimum touch target size */
  padding: 12px; /* Add padding for better touch interaction */
  font-size: 16px; /* Ensure text is readable */
  border-radius: 8px; /* Add rounded corners */
  border: 1px solid #ccc; /* Add subtle border */
  box-sizing: border-box; /* Include padding and border in total width */
}

input:focus {
  outline: none; /* Remove default outline */
  border-color: #007aff; /* Use Apple's blue for focus */
  box-shadow: 0 0 5px rgba(0, 122, 255, 0.5); /* Add blue glow on focus */
}

header h1 {
  /* ...existing code... */
}

@media screen and (max-width: 480px) {
  header h1 {
    text-align: left; /* Align the H1 logo to the left */
    margin-left: 5%; /* Add a consistent left margin for smaller screens */
  }
}

.app-header {
  position: fixed !important; /* Fix the header at the top of the screen */
  top: 0;
  z-index: 10000; /* Ensure it stays above all other elements */
  width: 100%; /* Full width */
  background-color: black; /* Black header */
}
