/* filepath: /src/components/AppHeader.css */
.app-header {
  position: fixed; /* Fix the header at the top */
  top: 0;
  z-index: 10000; /* Ensure it stays above all other elements */
  width: 100%; /* Full width */
  background-color: black; /* Black background */
  display: flex;
  align-items: center; /* Center items vertically */
  justify-content: center; /* Center the logo horizontally */
  padding: 10px 20px; /* Add padding for spacing */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); /* Subtle shadow */
}

body {
  margin: 0;
  padding: 0;
  padding-top: 60px; /* Offset for the fixed header */
  background-color: white; /* Ensure the body background is white */
}

.app-logo {
  display: flex;
  align-items: center; /* Center logo vertically */
  justify-content: center; /* Center logo horizontally */
  color: black; /* Black text for the logo */
  font-family: 'Ferrovial', sans-serif;
  font-size: 24px; /* Adjust font size */
}

.app-logo h1 {
  margin-left: 10px; /* Add spacing between logo and text */
  font-size: 24px; /* Adjust font size */
  color: yellow !important; /* Ensure yellow text for the logo */
}

.language-toggle {
  margin-left: auto; /* Push the button to the right */
  padding: 8px 16px; /* Add padding for better touch interaction */
  font-size: 16px; /* Ensure readability */
  border: none; /* Remove border */
  border-radius: 4px; /* Add rounded corners */
  background-color: #007aff; /* Button color */
  color: white; /* Text color */
  cursor: pointer; /* Add pointer cursor */
}

.language-toggle:active {
  background-color: #005bb5; /* Darken button color on press */
}

@media print {
  .app-header {
    display: block !important; /* Ensure the header is visible even when printing */
  }
}

@media screen and (max-width: 480px) {
  .app-logo {
    text-align: left; /* Align logo to the left */
    margin-left: 5%; /* Add 5% left margin */
  }
  #google_translate_element {
    position: absolute;
    top: 70px; /* Push below the black header and logo */
    left: 5%;
    z-index: 999; /* Ensure it stays visible */
  }

  .app-logo h1 {
    font-size: 18px; /* Adjust font size for smaller screens */
  }

  .language-toggle {
    font-size: 14px; /* Adjust font size for smaller screens */
    padding: 6px 12px; /* Adjust padding for smaller screens */
  }
}